import React, { useState } from 'react';
import PropTypes from 'prop-types';
import faker from 'faker';
import { filter } from 'lodash';

import { useTheme } from '@material-ui/core/styles';
import {
  Box,
  Checkbox,
  Grid,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TablePagination,
  TableRow,
  Typography,
} from '@material-ui/core';

import UserListToolbar from './UserListToolbar';
import UserListTableHead from './UserListTableHead';
import UserMoreMenu from './UserMoreMenu';
import SearchNotFound from '../../SearchNotFound';

const TABLE_HEAD = [
  { id: 'username', label: 'Username', alignRight: false },
  { id: 'totalReach', label: 'Total Reach', alignRight: false },
  { id: 'engagementRate', label: 'Engagement Rate', alignRight: false },
  { id: 'dateJoined', label: 'Date Joined', alignRight: false },
  { id: 'ticketsEarned', label: 'Tickets Earned', alignRight: false },
  { id: 'tokensEarned', label: 'Tokens Earned', alignRight: false },
  { id: 'starsEarned', label: 'Stars Earned', alignRight: false },
  { id: 'content', label: 'Content', alignRight: false },
  { id: 'caption', label: 'Caption', alignRight: false },
  { id: '' },
];

const userList = [...Array(100)].map((_, index) => {
  const setIndex = index + 1;
  return {
    id: `id ${setIndex}`,
    username: faker.internet.userName(),
    totalReach: faker.datatype.number({ min: 1000, max: 500000, precision: 1 }),
    engagementRate: faker.datatype.number({ min: 1, max: 100, precision: 1 }),
    dateJoined: faker.date.past(),
    ticketsEarned: faker.datatype.number({ min: 10, max: 100, precision: 1 }),
    tokensEarned: faker.datatype.number({ min: 100, max: 10000, precision: 1 }),
    starsEarned: faker.datatype.number({ min: 1000, max: 10000, precision: 1 }),
    content: faker.commerce.productName(),
    caption: faker.commerce.productName(),
  };
});

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function applySortFilter(array, comparator, query) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  if (query) {
    return filter(array, (_user) => _user.username.toLowerCase().indexOf(query.toLowerCase()) !== -1);
  }
  return stabilizedThis.map((el) => el[0]);
}

UserListTable.propTypes = {};

function UserListTable() {
  const theme = useTheme();
  const [page, setPage] = useState(0);
  const [order, setOrder] = useState('asc');
  const [selected, setSelected] = useState([]);
  const [orderBy, setOrderBy] = useState('username');
  const [filterName, setFilterName] = useState('');
  const [rowsPerPage, setRowsPerPage] = useState(5);

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = userList.map((n) => n.username);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event, username) => {
    const selectedIndex = selected.indexOf(username);
    let newSelected = [];
    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, username);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(selected.slice(0, selectedIndex), selected.slice(selectedIndex + 1));
    }
    setSelected(newSelected);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleFilterByName = (event) => {
    setFilterName(event.target.value);
  };

  const handleDeleteUser = (userId) => {
    console.log(`deleted user ${userId}`);
  };

  const emptyRows = page > 0 ? Math.max(0, (1 + page) * rowsPerPage - userList.length) : 0;

  const filteredUsers = applySortFilter(userList, getComparator(order, orderBy), filterName);

  const isUserNotFound = filteredUsers.length === 0;

  console.log('empty', emptyRows);

  return (
    <Grid container spacing={2} sx={{ mt: theme.spacing(8) }}>
      <Grid item xs={12}>
        <Typography variant="h5">User List</Typography>
      </Grid>
      <Grid item xs={12}>
        <UserListToolbar numSelected={selected.length} filterName={filterName} onFilterName={handleFilterByName} />
        <TableContainer sx={{ minWidth: 800 }}>
          <Table aria-label="user table">
            <UserListTableHead
              order={order}
              orderBy={orderBy}
              headLabel={TABLE_HEAD}
              rowCount={userList.length}
              numSelected={selected.length}
              onRequestSort={handleRequestSort}
              onSelectAllClick={handleSelectAllClick}
            />
            <TableBody>
              {filteredUsers.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                const {
                  id,
                  username,
                  totalReach,
                  engagementRate,
                  dateJoined,
                  ticketsEarned,
                  tokensEarned,
                  starsEarned,
                  content,
                  caption,
                } = row;

                const isItemSelected = selected.indexOf(username) !== -1;
                return (
                  <TableRow
                    hover
                    key={id}
                    tabIndex={-1}
                    role="checkbox"
                    selected={isItemSelected}
                    aria-checked={isItemSelected}
                  >
                    <TableCell padding="checkbox">
                      <Checkbox checked={isItemSelected} onChange={(event) => handleClick(event, username)} />
                    </TableCell>
                    <TableCell>{username}</TableCell>
                    <TableCell>{totalReach}</TableCell>
                    <TableCell>{`${engagementRate} %`}</TableCell>
                    <TableCell>{dateJoined.toDateString()}</TableCell>
                    <TableCell>{ticketsEarned}</TableCell>
                    <TableCell>{tokensEarned}</TableCell>
                    <TableCell>{starsEarned}</TableCell>
                    <TableCell>{content}</TableCell>
                    <TableCell>{caption}</TableCell>
                    <TableCell align="right">
                      <UserMoreMenu onDelete={() => handleDeleteUser(id)} userName={username} />
                    </TableCell>
                  </TableRow>
                );
              })}
              {emptyRows > 0 && (
                <TableRow style={{ height: 53 * emptyRows }}>
                  <TableCell colSpan={6} />
                </TableRow>
              )}
            </TableBody>
            {isUserNotFound && (
              <TableBody>
                <TableRow>
                  <TableCell align="center" colSpan={6} sx={{ py: 3 }}>
                    <SearchNotFound searchQuery={filterName} />
                  </TableCell>
                </TableRow>
              </TableBody>
            )}
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[5, 10, 25]}
          component="div"
          count={userList.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Grid>
    </Grid>
  );
}

export default UserListTable;
